class Cookiebar extends HTMLElement {
	connectedCallback() {
		this.innerHTML = `
			<gov-cookiebar>
				<h2 class="h4">Meme si u vs uloit cookies?</h2>

				<p>
					Co e to znamen? Cookies jsou mal datov soubory, kter slou nap. k tomu, aby si web pamatoval vae nastaven a to, co vs zajm, nebo abychom jej zlepovali . Pro ukldn rznch typ cookies od vs potebujeme souhlas. Web bude fungovat i bez souhlasu, s nm ale o nco lpe.
				</p>

				<gov-button variant="primary" slot="actions-primary">
					Souhlasm se vemi
				</gov-button>

				<gov-button variant="primary" type="outlined" slot="actions-primary">
					Odmtnout vechny
				</gov-button>

				<gov-button variant="primary" type="base" slot="actions-secondary">
					Podrobn nastaven
				</gov-button>
			</gov-cookiebar>
		`
	}
}

if (customElements.get('cookiebar-page') === undefined) {
	customElements.define('cookiebar-page', Cookiebar)
}
